













































import { Component, Prop, Vue, Watch } from 'vue-property-decorator'

@Component
export default class Cell extends Vue {
  @Prop()
  content!: string

  @Prop()
  i: any

  @Prop()
  j: any

  @Prop()
  type: any

  @Prop()
  index: any

  contentCopy = {}

  selection: any = {
    0: '字段名称',
    1: '字段',
    2: '详细说明',
  }

  change() {
    this.contentCopy = !this.contentCopy
  }

  valChange() {
    this.contentCopy = this.selection[this.index[this.j]]
  }
  mounted() {
    this.contentCopy = this.content
  }

  @Watch('content')
  onContentChanged(newval: any, oldval: any) {
    this.contentCopy = newval
  }

  @Watch('contentCopy')
  onValueChanged(newval: any, oldval: any) {
    if (newval === 'true') {
      newval = true
    }

    if (newval === 'false') {
      newval = false
    }

    this.$emit('update', newval)
  }

  cellPaste(e: ClipboardEvent) {
    if (this.type !== 'checkBox') {
      var text = e.clipboardData!.getData('text')

      this.$emit('cellPaste', text)
    }
  }

  cellTranslate() {
    this.$emit('cellTranslate', this.i, this.j)
  }

  cellDelete() {
    this.$emit('cellDelete', this.i, this.j)
  }

  cancle(e: any) {
    console.log(e)
  }
}
